
'use client';

import React, { useEffect, useState, useRef } from 'react';
import { useAuth } from '@/context/AuthContext';
import { api } from '@/services/api';
import {
    Package,
    Truck,
    CheckCircle2,
    Clock,
    LogOut,
    User as UserIcon,
    Phone,
    Mail,
    ChevronRight,
    Search,
    AlertCircle,
    ShoppingBag,
    MapPin,
    Plus,
    Trash2,
    Home,
    Edit2,
    Loader2,
    Crosshair,
    Info,
    CheckCircle,
    UserCircle,
    Map
} from 'lucide-react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { cn } from '@/lib/utils';
import { format } from 'date-fns';
import { fr } from 'date-fns/locale';
import { useUI } from '@/context/UIContext';

export default function AccountPage() {
    const { user, addresses, logout, loading: authLoading, refreshAddresses, updateProfile } = useAuth();
    const [orders, setOrders] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [showAddAddress, setShowAddAddress] = useState(false);
    const [editingAddressId, setEditingAddressId] = useState<string | null>(null);
    const [newAddress, setNewAddress] = useState({ label: '', address_full: '', lat: 0, lon: 0 });

    // Profile Edit State
    const [isEditingProfile, setIsEditingProfile] = useState(false);
    const [profileData, setProfileData] = useState({ name: '', phone: '', email: '' });
    const [isUpdatingProfile, setIsUpdatingProfile] = useState(false);

    // Address Search State (Nominatim)
    const [suggestions, setSuggestions] = useState<any[]>([]);
    const [showSuggestions, setShowSuggestions] = useState(false);
    const [isSearching, setIsSearching] = useState(false);

    // Map Refs
    const mapRef = useRef<any>(null);
    const markerRef = useRef<any>(null);
    const mapContainerRef = useRef<HTMLDivElement>(null);
    const addressInputRef = useRef<HTMLInputElement>(null);

    const router = useRouter();

    useEffect(() => {
        if (!authLoading && !user) {
            router.push('/login?redirect=/account');
        }

        if (user) {
            api.getOrders(user.id)
                .then(res => setOrders(res.orders))
                .catch(err => console.error('Error fetching orders:', err))
                .finally(() => setLoading(false));

            setProfileData({
                name: user.name || '',
                phone: user.phone || '',
                email: user.email || ''
            });
        }
    }, [user, authLoading, router]);

    const handleUpdateProfile = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsUpdatingProfile(true);
        try {
            await updateProfile(profileData);
            setIsEditingProfile(false);
        } catch (err: any) {
            console.error('Error updating profile:', err);
            alert(err.response?.data?.message || 'Erreur lors de la mise à jour');
        } finally {
            setIsUpdatingProfile(false);
        }
    };

    // Address Search Logic
    const handleAddressChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value;
        setNewAddress(prev => ({ ...prev, address_full: value }));

        if (value.length < 3) {
            setSuggestions([]);
            return;
        }

        setIsSearching(true);
        try {
            const response = await fetch(
                `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(value)}&countrycodes=ci&limit=5`
            );
            const data = await response.json();
            setSuggestions(data);
            setShowSuggestions(true);
        } catch (err) {
            console.error("Geocoding error:", err);
        } finally {
            setIsSearching(false);
        }
    };

    const handleSuggestionClick = (place: any) => {
        const lat = parseFloat(place.lat);
        const lon = parseFloat(place.lon);
        setNewAddress(prev => ({
            ...prev,
            address_full: place.display_name,
            lat,
            lon
        }));
        setShowSuggestions(false);

        if (mapRef.current) {
            mapRef.current.setView([lat, lon], 16);
            if (markerRef.current) {
                markerRef.current.setLatLng([lat, lon]);
            }
        }
    };

    const handleGeolocation = () => {
        if (!navigator.geolocation) {
            alert("La géolocalisation n'est pas supportée par votre navigateur.");
            return;
        }

        navigator.geolocation.getCurrentPosition(async (position) => {
            const { latitude, longitude } = position.coords;
            try {
                const response = await fetch(
                    `https://nominatim.openstreetmap.org/reverse?format=json&lat=${latitude}&lon=${longitude}`
                );
                const data = await response.json();
                setNewAddress(prev => ({
                    ...prev,
                    address_full: data.display_name || "Ma position",
                    lat: latitude,
                    lon: longitude
                }));
                if (mapRef.current) {
                    mapRef.current.setView([latitude, longitude], 16);
                    if (markerRef.current) {
                        markerRef.current.setLatLng([latitude, longitude]);
                    }
                }
            } catch (err) {
                console.error("Geocoding current position failed:", err);
            }
        }, (error) => {
            console.error("Geolocation error:", error);
            alert("Impossible de récupérer votre position.");
        });
    };

    useEffect(() => {
        if (!showAddAddress || !mapContainerRef.current) return;

        const L = (window as any).L;
        if (!L) return;

        const initialLat = newAddress.lat || 5.3484;
        const initialLon = newAddress.lon || -4.0305;

        const timer = setTimeout(() => {
            try {
                if (mapRef.current) {
                    mapRef.current.remove();
                }

                mapRef.current = L.map(mapContainerRef.current).setView([initialLat, initialLon], 13);
                L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png').addTo(mapRef.current);

                markerRef.current = L.marker([initialLat, initialLon], { draggable: true }).addTo(mapRef.current);

                markerRef.current.on('dragend', async () => {
                    const pos = markerRef.current.getLatLng();
                    setNewAddress(prev => ({ ...prev, lat: pos.lat, lon: pos.lng }));

                    try {
                        const res = await fetch(`https://nominatim.openstreetmap.org/reverse?format=json&lat=${pos.lat}&lon=${pos.lng}`);
                        const data = await res.json();
                        setNewAddress(prev => ({ ...prev, address_full: data.display_name }));
                    } catch (e) { }
                });
            } catch (err) { }
        }, 300);

        return () => clearTimeout(timer);
    }, [showAddAddress]);

    const handleAddAddress = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user) return;
        try {
            if (editingAddressId) {
                await api.updateAddress(user.id, editingAddressId, newAddress);
            } else {
                await api.addAddress(user.id, {
                    ...newAddress,
                    is_default: addresses.length === 0
                });
            }
            setNewAddress({ label: '', address_full: '', lat: 0, lon: 0 });
            setShowAddAddress(false);
            setEditingAddressId(null);
            await refreshAddresses();
        } catch (err) {
            console.error('Error saving address:', err);
        }
    };

    const startEdit = (addr: any) => {
        setNewAddress({
            label: addr.label,
            address_full: addr.address_full,
            lat: addr.lat,
            lon: addr.lon
        });
        setEditingAddressId(addr.id);
        setShowAddAddress(true);
    };

    const handleDeleteAddress = async (id: string) => {
        if (!user) return;
        try {
            await api.deleteAddress(user.id, id);
            await refreshAddresses();
        } catch (err) {
            console.error('Error deleting address:', err);
        }
    };

    const handleSetDefault = async (id: string) => {
        if (!user) return;
        try {
            await api.setDefaultAddress(user.id, id);
            await refreshAddresses();
        } catch (err) {
            console.error('Error setting default address:', err);
        }
    };

    const handleLogout = () => {
        logout();
        router.push('/');
    };

    if (authLoading || (loading && !orders.length)) {
        return (
            <div className="min-h-screen bg-white flex items-center justify-center">
                <div className="flex flex-col items-center gap-4">
                    <Loader2 className="w-8 h-8 text-slate-900 animate-spin" />
                    <p className="text-slate-400 font-bold uppercase tracking-[0.2em] text-[10px]">Chargement de votre compte...</p>
                </div>
            </div>
        );
    }

    const getStatusInfo = (status: string) => {
        switch (status.toLowerCase()) {
            case 'paid':
                return { label: 'Payé', color: 'text-emerald-700 bg-emerald-50', icon: CheckCircle2 };
            case 'delivered':
                return { label: 'Livré', color: 'text-slate-700 bg-slate-50', icon: Truck };
            case 'cancelled':
                return { label: 'Annulé', color: 'text-rose-700 bg-rose-50', icon: AlertCircle };
            default:
                return { label: 'En attente', color: 'text-amber-700 bg-amber-50', icon: Clock };
        }
    };

    return (
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
            <div className="grid lg:grid-cols-12 gap-12 items-start">

                {/* Sidebar / Profile Info */}
                <div className="lg:col-span-4 space-y-8">
                    <div className="bg-white rounded-[2.5rem] p-8 shadow-2xl shadow-slate-200/50 border border-slate-100 overflow-hidden relative group">
                        <div className="absolute -top-12 -right-12 p-6 opacity-[0.03] group-hover:scale-110 transition-transform">
                            <UserIcon className="w-64 h-64" />
                        </div>

                        <div className="relative z-10">
                            <div className="flex items-center justify-between mb-8">
                                <div className="w-16 h-16 bg-slate-900 rounded-2xl flex items-center justify-center text-white shadow-xl shadow-slate-900/10">
                                    <UserIcon className="w-8 h-8" />
                                </div>
                                {!isEditingProfile && (
                                    <button
                                        onClick={() => setIsEditingProfile(true)}
                                        className="p-3 hover:bg-slate-50 rounded-xl text-slate-400 hover:text-slate-900 transition-all border border-transparent hover:border-slate-100"
                                    >
                                        <Edit2 className="w-5 h-5" />
                                    </button>
                                )}
                            </div>

                            {isEditingProfile ? (
                                <form onSubmit={handleUpdateProfile} className="space-y-4 animate-in fade-in slide-in-from-top-4 duration-300">
                                    <div className="space-y-1">
                                        <label className="text-[10px] font-black uppercase tracking-widest text-slate-400 ml-1">Nom Complet</label>
                                        <input
                                            type="text"
                                            value={profileData.name}
                                            onChange={e => setProfileData({ ...profileData, name: e.target.value })}
                                            className="w-full bg-slate-50 border-2 border-slate-100 rounded-xl px-4 py-3 text-sm focus:border-slate-900 transition-all outline-none font-bold"
                                            required
                                        />
                                    </div>
                                    <div className="space-y-1">
                                        <label className="text-[10px] font-black uppercase tracking-widest text-slate-400 ml-1">Téléphone</label>
                                        <input
                                            type="tel"
                                            value={profileData.phone}
                                            onChange={e => setProfileData({ ...profileData, phone: e.target.value })}
                                            className="w-full bg-slate-50 border-2 border-slate-100 rounded-xl px-4 py-3 text-sm focus:border-slate-900 transition-all outline-none font-bold"
                                            required
                                        />
                                    </div>
                                    <div className="space-y-1">
                                        <label className="text-[10px] font-black uppercase tracking-widest text-slate-400 ml-1">Email (Optionnel)</label>
                                        <input
                                            type="email"
                                            value={profileData.email}
                                            onChange={e => setProfileData({ ...profileData, email: e.target.value })}
                                            className="w-full bg-slate-50 border-2 border-slate-100 rounded-xl px-4 py-3 text-sm focus:border-slate-900 transition-all outline-none font-bold"
                                        />
                                    </div>
                                    <div className="flex gap-2 pt-2">
                                        <button
                                            type="submit"
                                            disabled={isUpdatingProfile}
                                            className="flex-1 bg-slate-900 text-white font-black py-3 rounded-xl text-xs uppercase tracking-widest hover:bg-slate-800 disabled:opacity-50 transition-all shadow-lg shadow-slate-900/10"
                                        >
                                            {isUpdatingProfile ? <Loader2 className="w-4 h-4 animate-spin mx-auto" /> : 'Enregistrer'}
                                        </button>
                                        <button
                                            type="button"
                                            onClick={() => setIsEditingProfile(false)}
                                            className="px-4 py-3 border-2 border-slate-100 rounded-xl text-xs font-black text-slate-400 uppercase tracking-widest hover:bg-slate-50 transition-all"
                                        >
                                            Annuler
                                        </button>
                                    </div>
                                </form>
                            ) : (
                                <div className="animate-in fade-in duration-300">
                                    <h2 className="text-3xl font-black text-slate-900 tracking-tight leading-none mb-2">{user?.name}</h2>
                                    <p className="text-slate-400 text-xs font-bold uppercase tracking-widest mb-6">Membre Privilège</p>

                                    <div className="space-y-4 pt-6 border-t border-slate-50">
                                        <div className="flex items-center gap-4 group/item">
                                            <div className="w-10 h-10 bg-slate-50 rounded-xl flex items-center justify-center text-slate-400 group-hover/item:bg-slate-900 group-hover/item:text-white transition-all">
                                                <Phone className="w-4 h-4" />
                                            </div>
                                            <div>
                                                <p className="text-[10px] font-black text-slate-300 uppercase tracking-widest">Téléphone</p>
                                                <p className="text-sm font-bold text-slate-600">{user?.phone}</p>
                                            </div>
                                        </div>
                                        {user?.email && (
                                            <div className="flex items-center gap-4 group/item">
                                                <div className="w-10 h-10 bg-slate-50 rounded-xl flex items-center justify-center text-slate-400 group-hover/item:bg-slate-900 group-hover/item:text-white transition-all">
                                                    <Mail className="w-4 h-4" />
                                                </div>
                                                <div>
                                                    <p className="text-[10px] font-black text-slate-300 uppercase tracking-widest">Email</p>
                                                    <p className="text-sm font-bold text-slate-600">{user?.email}</p>
                                                </div>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            )}

                            <button
                                onClick={handleLogout}
                                className="mt-8 flex items-center gap-2 text-rose-500 font-black text-[10px] uppercase tracking-[0.2em] hover:text-rose-600 transition-colors w-full pt-6 border-t border-slate-50 group/logout"
                            >
                                <LogOut className="w-4 h-4 group-hover:-translate-x-1 transition-transform" />
                                Se déconnecter
                            </button>
                        </div>
                    </div>

                    {/* Addresses Section */}
                    <div className="bg-white rounded-[2.5rem] p-8 shadow-2xl shadow-slate-200/50 border border-slate-100">
                        <div className="flex items-center justify-between mb-8">
                            <div>
                                <h3 className="text-xl font-black text-slate-900 tracking-tight">Mes Adresses</h3>
                                <p className="text-[10px] font-bold text-slate-400 uppercase tracking-widest mt-1">Lieux de livraison</p>
                            </div>
                            <button
                                onClick={() => {
                                    setEditingAddressId(null);
                                    setNewAddress({ label: '', address_full: '', lat: 5.3484, lon: -4.0305 });
                                    setShowAddAddress(!showAddAddress);
                                }}
                                className="w-12 h-12 bg-slate-900 text-white rounded-2xl flex items-center justify-center hover:scale-110 transition-all shadow-xl shadow-slate-900/20"
                            >
                                <Plus className="w-6 h-6" />
                            </button>
                        </div>

                        {showAddAddress && (
                            <form onSubmit={handleAddAddress} className="mb-8 p-6 bg-slate-50 rounded-3xl border-2 border-slate-100 space-y-4 animate-in fade-in slide-in-from-top-4 duration-400 relative">
                                <div className="space-y-1">
                                    <label className="text-[10px] font-black uppercase tracking-widest text-slate-400 ml-1">Libellé (ex: Bureau, Maison)</label>
                                    <input
                                        type="text"
                                        placeholder="Bureau, Domicile..."
                                        required
                                        value={newAddress.label}
                                        onChange={(e) => setNewAddress({ ...newAddress, label: e.target.value })}
                                        className="w-full bg-white border-2 border-slate-100 rounded-xl px-4 py-3 text-sm focus:border-slate-900 outline-none font-bold"
                                    />
                                </div>

                                <div className="space-y-1 relative">
                                    <label className="text-[10px] font-black uppercase tracking-widest text-slate-400 ml-1">Adresse Complète</label>
                                    <div className="relative group">
                                        <input
                                            ref={addressInputRef}
                                            type="text"
                                            placeholder="Saisissez votre quartier..."
                                            required
                                            value={newAddress.address_full}
                                            onChange={handleAddressChange}
                                            className="w-full bg-white border-2 border-slate-100 rounded-xl px-4 py-3 pl-10 text-sm focus:border-slate-900 outline-none font-bold pr-12"
                                        />
                                        <MapPin className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-300" />

                                        <button
                                            type="button"
                                            onClick={handleGeolocation}
                                            className="absolute right-3 top-1/2 -translate-y-1/2 p-1.5 hover:bg-slate-50 rounded-lg text-slate-400 hover:text-slate-900 transition-all"
                                        >
                                            <Crosshair className="w-4 h-4" />
                                        </button>

                                        {showSuggestions && suggestions.length > 0 && (
                                            <div className="absolute top-full left-0 right-0 mt-2 bg-white border-2 border-slate-100 rounded-2xl shadow-2xl z-[60] overflow-hidden animate-in fade-in zoom-in-95 duration-200">
                                                {suggestions.map((place, idx) => (
                                                    <button
                                                        key={idx}
                                                        type="button"
                                                        onClick={() => handleSuggestionClick(place)}
                                                        className="w-full p-4 text-left hover:bg-slate-50 flex items-start gap-3 transition-colors border-b border-slate-50 last:border-0"
                                                    >
                                                        <MapPin className="w-4 h-4 text-slate-200 mt-1 flex-shrink-0" />
                                                        <p className="text-xs text-slate-600 font-bold">{place.display_name}</p>
                                                    </button>
                                                ))}
                                            </div>
                                        )}
                                    </div>
                                </div>

                                <div className="relative rounded-2xl overflow-hidden border-2 border-slate-100 h-48 bg-slate-200">
                                    <div ref={mapContainerRef} className="h-full w-full" />
                                    <div className="absolute bottom-2 left-2 right-2 pointer-events-none">
                                        <div className="bg-white/90 backdrop-blur px-3 py-1.5 rounded-lg border border-slate-100 shadow-sm inline-flex items-center gap-2">
                                            <Info className="w-3 h-3 text-slate-400" />
                                            <span className="text-[9px] font-black text-slate-500 uppercase tracking-tight">Déplacez le marqueur</span>
                                        </div>
                                    </div>
                                </div>

                                <div className="flex gap-2 pt-2">
                                    <button type="submit" className="flex-1 bg-slate-900 text-white py-3 rounded-xl text-xs font-black uppercase tracking-widest hover:bg-slate-800 transition-all shadow-lg shadow-slate-900/10">
                                        {editingAddressId ? 'Modifier' : 'Enregistrer'}
                                    </button>
                                    <button
                                        type="button"
                                        onClick={() => {
                                            setShowAddAddress(false);
                                            setEditingAddressId(null);
                                            setNewAddress({ label: '', address_full: '', lat: 0, lon: 0 });
                                        }}
                                        className="px-6 py-3 border-2 border-slate-100 rounded-xl text-xs font-black text-slate-400 uppercase tracking-widest hover:bg-slate-50 transition-all"
                                    >
                                        Annuler
                                    </button>
                                </div>
                            </form>
                        )}

                        <div className="space-y-4">
                            {addresses.length === 0 ? (
                                <div className="text-center py-12 px-6 border-2 border-dashed border-slate-100 rounded-3xl">
                                    <Map className="w-12 h-12 text-slate-100 mx-auto mb-4" />
                                    <p className="text-xs font-bold text-slate-300 uppercase tracking-widest">Aucune adresse enregistrée</p>
                                </div>
                            ) : (
                                addresses.map((addr) => (
                                    <div key={addr.id} className="group relative bg-slate-50 hover:bg-white p-5 rounded-[2rem] border-2 border-transparent hover:border-slate-100 transition-all duration-300">
                                        <div className="flex items-start justify-between">
                                            <div className="flex items-center gap-4">
                                                <div className={cn(
                                                    "w-12 h-12 rounded-2xl flex items-center justify-center transition-all",
                                                    addr.is_default ? "bg-slate-900 text-white shadow-lg shadow-slate-900/10" : "bg-white text-slate-400 border-2 border-slate-100"
                                                )}>
                                                    <Home className="w-5 h-5" />
                                                </div>
                                                <div className="min-w-0">
                                                    <p className="text-sm font-black text-slate-900">{addr.label}</p>
                                                    <p className="text-[10px] text-slate-400 font-bold truncate max-w-[150px] uppercase tracking-tighter">{addr.address_full}</p>
                                                </div>
                                            </div>
                                            <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-all">
                                                <button
                                                    onClick={() => startEdit(addr)}
                                                    className="p-2 text-slate-400 hover:text-slate-900 hover:bg-slate-50 rounded-lg transition-all"
                                                >
                                                    <Edit2 className="w-4 h-4" />
                                                </button>
                                                <button
                                                    onClick={() => handleDeleteAddress(addr.id)}
                                                    className="p-2 text-rose-400 hover:text-rose-600 hover:bg-rose-50 rounded-lg transition-all"
                                                >
                                                    <Trash2 className="w-4 h-4" />
                                                </button>
                                            </div>
                                        </div>
                                        {!addr.is_default && (
                                            <button
                                                onClick={() => handleSetDefault(addr.id)}
                                                className="mt-4 text-[9px] font-black text-slate-400 uppercase tracking-[0.2em] hover:text-slate-900 transition-colors"
                                            >
                                                Définir par défaut
                                            </button>
                                        )}
                                    </div>
                                ))
                            )}
                        </div>
                    </div>
                </div>

                {/* Order History */}
                <div className="lg:col-span-8 space-y-8">
                    <div className="flex items-center justify-between">
                        <div>
                            <h2 className="text-3xl font-black text-slate-900 tracking-tight">Historique des Commandes</h2>
                            <p className="text-[10px] font-black uppercase tracking-[0.3em] text-slate-300 mt-1">Suivi des achats</p>
                        </div>
                        <div className="hidden sm:flex items-center gap-2 px-4 py-2 bg-slate-50 rounded-full border border-slate-100">
                            <div className="w-2 h-2 bg-slate-900 rounded-full animate-pulse" />
                            <span className="text-[10px] font-black uppercase tracking-widest text-slate-400">{orders.length} Commandes</span>
                        </div>
                    </div>

                    {orders.length === 0 ? (
                        <div className="bg-white rounded-[3rem] p-24 text-center shadow-2xl shadow-slate-200/50 border border-slate-100">
                            <div className="w-32 h-32 bg-slate-50 rounded-full flex items-center justify-center mx-auto mb-8 border-2 border-slate-100">
                                <ShoppingBag className="w-16 h-16 text-slate-200" />
                            </div>
                            <h3 className="text-2xl font-black text-slate-900 mb-2">Votre panier est vide</h3>
                            <p className="text-slate-400 font-bold mb-10 max-w-xs mx-auto">Commencez dès maintenant à prendre soin de votre santé avec nos protocoles validés.</p>
                            <Link href="/" className="bg-slate-900 text-white px-10 py-5 rounded-[2rem] font-black text-xs uppercase tracking-[0.2em] hover:scale-105 active:scale-95 transition-all inline-block shadow-2xl shadow-slate-900/20">
                                Découvrir les Produits
                            </Link>
                        </div>
                    ) : (
                        <div className="grid gap-6">
                            {orders.map((order) => {
                                const { label, color, icon: Icon } = getStatusInfo(order.status);
                                return (
                                    <Link
                                        key={order.id}
                                        href={`/payment/${order.id}`}
                                        className="bg-white rounded-[2.5rem] p-8 shadow-sm hover:shadow-2xl hover:shadow-slate-200/50 border border-slate-100 transition-all duration-500 group flex flex-col md:flex-row md:items-center gap-8 relative overflow-hidden"
                                    >
                                        <div className="flex-1 space-y-4">
                                            <div className="flex flex-wrap items-center gap-4">
                                                <span className="text-[10px] font-black text-slate-300 uppercase tracking-[0.2em]">Réf: #{order.id.slice(0, 8)}</span>
                                                <span className={cn("px-4 py-1.5 rounded-full text-[9px] font-black uppercase tracking-[0.15em] flex items-center gap-2", color)}>
                                                    <Icon className="w-3.5 h-3.5" />
                                                    {label}
                                                </span>
                                            </div>
                                            <div>
                                                <p className="text-[10px] font-black text-slate-400 uppercase tracking-widest mb-1">Total Commande</p>
                                                <h3 className="text-3xl font-black text-slate-900 tracking-tighter">
                                                    {Number(order.total).toLocaleString()} <span className="text-sm font-bold text-slate-300">FCFA</span>
                                                </h3>
                                            </div>
                                            <div className="flex items-center gap-2 text-[10px] font-bold text-slate-400 uppercase tracking-widest">
                                                <Clock className="w-3 h-3" />
                                                {format(new Date(order.created_at), "d MMMM yyyy 'à' HH:mm", { locale: fr })}
                                            </div>
                                        </div>

                                        <div className="flex items-center justify-between md:justify-end gap-10 border-t md:border-none pt-6 md:pt-0">
                                            <div className="md:text-right">
                                                <p className="text-[10px] font-black text-slate-300 uppercase tracking-widest mb-2">Expédié à</p>
                                                <p className="text-xs font-black text-slate-600 truncate max-w-[180px]">{order.address_full}</p>
                                            </div>
                                            <div className="w-14 h-14 bg-slate-50 border-2 border-slate-100 rounded-2xl flex items-center justify-center group-hover:bg-slate-900 group-hover:text-white group-hover:border-slate-900 transition-all duration-500">
                                                <ChevronRight className="w-6 h-6 group-hover:translate-x-1 transition-transform" />
                                            </div>
                                        </div>
                                    </Link>
                                );
                            })}
                        </div>
                    )}
                </div>

            </div>
        </div>
    );
}
